import axios from 'axios'

// Set hostname for dev or prod
export const HOST = process.env.NODE_ENV == "DEV" ? process.env.HOST : location.origin

// Create instance axios for REST API 
export const HTTP = axios.create({
  baseURL: location.origin //"http://127.0.0.1:8800"
})


export default {
  // Get all tasks f
  getAll() {
    return HTTP.get("/api/all_event")
  },
  // Create new event
  createEvent(payload) {
    return HTTP.post("/api/create_event", payload)
  },
  // Delete new event
  deleteEvent(payload) {
    return HTTP.post("/api/delete_event", payload)
  },
  // Гзвфеу new event
  updateEvent(payload) {
    return HTTP.post("/api/update_event", payload)
  }
}


