<template>
  <v-app>
    <v-toolbar color="#00acd7" dark app fixed>
      <v-toolbar-title style="width: 400px" class="ml-0 pl-3">
        <v-toolbar-side-icon>
          <v-avatar :size="40">
            <img src="../img/go.svg" />
          </v-avatar>
        </v-toolbar-side-icon>
        <span class="pl-2 badge1" :data-badge="version">
          <router-link :to="'/'" class="pef">Almanac</router-link>
        </span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon>
        <a href="https://github.com/mirecl/goalmanac">
          <img src="../img/github.svg" />
        </a>
      </v-btn>
    </v-toolbar>
    <v-content>
      <h1 class="text-xs-center pt-4">Список событий ({{getCnt}})</h1>
      <v-container fluid>
        <v-layout justify-center align-center>
          <v-data-table
            :headers="getHeader"
            :items="getTable"
            :rows-per-page-items="[10,20,30]"
            :rows-per-page-text="'Количество строк'"
            :pagination.sync="pagination"
            class="elevation-1"
            :hide-actions="getTable.length == 0"
          >
            <template slot="headerCell" slot-scope="props">
              <v-tooltip top>
                <template v-slot:activator="{ on }">
                  <span v-on="on">{{ props.header.text}}</span>
                </template>
                <span>{{ props.header.help}}</span>
              </v-tooltip>
            </template>
            <template v-slot:items="props">
              <td class="text-xs">
                <a @click="onClick(props.item.id)">{{ props.item.id }}</a>
              </td>
              <td class="text-xs">{{ props.item.user }}</td>
              <td class="text-xs">{{ props.item.title }}</td>
              <td class="text-xs-center">{{ props.item.start | formatDate}}</td>
              <td class="text-xs-center">{{ props.item.end | formatDate}}</td>
            </template>
            <template v-slot:no-data>
              <div style="text-align:center">События отсутствуют.</div>
            </template>
            <template
              slot="pageText"
              slot-scope="item"
            >{{item.pageStart}}-{{item.pageStop}} из {{item.itemsLength}}</template>
          </v-data-table>
        </v-layout>
      </v-container>
    </v-content>
    <v-btn fab bottom right color="#00acd7" dark fixed @click="dialog = !dialog">
      <v-icon>add</v-icon>
    </v-btn>
    <v-form ref="form">
      <v-dialog v-model="dialog" width="800px">
        <v-card>
          <v-card-title class="grey lighten-4 py-4 title">Создание события</v-card-title>
          <v-container grid-list-sm class="pa-4">
            <v-layout row wrap>
              <v-flex xs6>
                <datetime
                  type="datetime"
                  ref="dateTimePicker"
                  input-id="startDate"
                  v-model="datetime"
                  format="yyyy-MM-dd HH:mm"
                  class="theme-orange"
                  input-class="hid"
                  :minute-step="10"
                  :phrases="{ok: 'Выбор', cancel: 'Отмена'}"
                  :min-datetime="minDatetime"
                  :max-datetime="maxDatetime"
                ></datetime>
                <v-text-field
                  prepend-icon="calendar_today"
                  readonly
                  required
                  placeholder="Время начала"
                  :value="get_date"
                  :rules="rule"
                  @click="$refs.dateTimePicker.isOpen = true"
                ></v-text-field>
              </v-flex>
              <v-flex xs6>
                <v-select
                  v-model="select"
                  :items="items"
                  label="Укажите продолжительнсть собрания"
                  prepend-icon="access_time"
                  :rules="rule"
                  required
                ></v-select>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  prepend-icon="text_format"
                  placeholder="Тема"
                  :rules="rule"
                  required
                  v-model="title"
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  prepend-icon="supervised_user_circle"
                  placeholder="Пользователь"
                  v-model="user"
                  :rules="rule"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-textarea
                  label="Описание"
                  auto-grow
                  outlined
                  v-model="body"
                  rows="3"
                  row-height="50"
                  prepend-icon="textsms"
                  shaped
                  required
                  :rules="rule"
                ></v-textarea>
              </v-flex>
            </v-layout>
          </v-container>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" @click="onExit">Отменить</v-btn>
            <v-btn flat color="red" @click="onDelete" v-if="change">Удалить</v-btn>
            <v-btn flat @click="onChange" v-if="change">Изменить</v-btn>
            <v-btn flat @click="onSave" v-if="!change">Сохранить</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-form>
  </v-app>
</template>

<script>
import { Datetime } from "vue-datetime";
import { mapGetters } from "vuex";
export default {
  created() {
    this.$store.dispatch("getEvent");
  },
  computed: {
    ...mapGetters([
      "getHeader", // Получить заголовок таблицы
      "getTable", // Получить данные в таблицы
      "getCnt"
    ]),
    get_date() {
      console.log("daate");
      if (this.datetime == "") {
        return null;
      }
      var dt = new Date(this.datetime).toLocaleString();
      var date = dt.substring(0, 10);
      var time = dt.substring(11, 17);
      return date + " " + time;
    }
  },
  components: { Datetime },
  data: () => ({
    uid: "",
    change: false,
    title: "",
    user: "",
    body: "",
    select: "",
    items: ["10 мин", "20 мин", "30 мин", "40 мин", "50 мин", "60 мин"],
    pagination: { descending: false },
    vis: true,
    version: "0.3.0",
    dialog: false,
    datetime: "",
    minDatetime: "2019-11-03",
    maxDatetime: "2019-12-13",
    rule: [v => !!v || "Укажите значение"]
  }),
  filters: {
    formatDate(value) {
      return new Date(value).toLocaleString();
    }
  },
  methods: {
    onChange() {
      var dt = new Date(this.datetime);
      var payload = {
        id: this.uid,
        title: this.title,
        user: this.user,
        body: this.body,
        start: dt.toLocaleString(),
        duration: this.select.split(" ")[0] + "m"
      };
      this.$store.dispatch("changeEvent", payload);
      this.dialog = false;
      this.change = false;
      this.$refs.form.reset();
      this.user = "";
      this.datetime = "";
      this.title = "";
      this.body = "";
    },
    onDelete() {
      this.$store.dispatch("deleteEvent", { id: this.uid });
      this.dialog = false;
      this.change = false;
      this.$refs.form.reset();
      this.user = "";
      this.datetime = "";
      this.title = "";
      this.body = "";
    },
    onClick(id) {
      var table = this.getTable;
      table.forEach(row => {
        if (row.id == id) {
          this.user = row.user;
          this.datetime = row.start;
          this.title = row.title;
          this.body = row.body;
          var s = new Date(row.start);
          var e = new Date(row.end);
          var del = (e - s) / 60000;
          this.select = del + " мин";
        }
      });
      this.change = true;
      this.uid = id;
      this.dialog = true;
    },
    onExit() {
      this.dialog = false;
      this.change = false;
      this.$refs.form.reset();
      this.user = "";
      this.datetime = "";
      this.title = "";
      this.body = "";
    },
    onSave() {
      if (this.$refs.form.validate()) {
        var dt = new Date(this.datetime);
        this.$store.dispatch("createEvent", {
          title: this.title,
          user: this.user,
          body: this.body,
          start: dt.toLocaleString(),
          duration: this.select.split(" ")[0] + "m"
        });
        this.dialog = false;
        this.$refs.form.reset();
        this.user = "";
        this.datetime = "";
        this.title = "";
        this.body = "";
      }
    }
  },
  props: {
    source: String
  }
};
</script>


<style>
.badge1 {
  position: relative;
  top: 3px;
}

.badge1[data-badge]:after {
  content: attr(data-badge);
  position: absolute;
  top: -7px;
  font-size: 0.7em;
  color: white;
  text-align: center;
}
.pef {
  color: white;
  text-decoration: none;
}

.theme-orange .vdatetime-popup__header,
.theme-orange .vdatetime-calendar__month__day--selected > span > span,
.theme-orange .vdatetime-calendar__month__day--selected:hover > span > span {
  background: #00acd7;
}

.theme-orange .vdatetime-year-picker__item--selected,
.theme-orange .vdatetime-time-picker__item--selected,
.theme-orange .vdatetime-popup__actions__button {
  color: #00acd7;
  left: 10px;
}
.hid {
  display: none;
}

.primary--text {
  color: #00acd7 !important;
  caret-color: #00acd7 !important;
}
</style>


