import api from '@/utils/HTTP'
import { stat } from 'fs'

export default {
    state: {
        // Заголовки таблицы
        headers: [
            {
                text: "GUID",
                align: "left",
                value: "id",
                help: 'Порядковый номер события',
                width: "320",
                sortable: false
            },
            { text: "Пользователь", value: "user", align: "left", sortable: false, help: 'Юзер', width: "160" },
            { text: "Тема", value: "title", align: "left", sortable: false, help: 'Тема', width: "150" },
            { text: "Время начала", value: "start", sortable: true, help: 'Время начала события' },
            { text: "Время окончания", value: "end", sortable: false, help: 'Время окончания события' }
        ],
        eventTable: [] // Данные таблицы 
    },
    getters: {
        getHeader(state) {
            return state.headers // Получить Header 
        },
        getTable(state) {
            return state.eventTable // Получить таблицу 
        },
        getCnt(state){
            return state.eventTable.length
        }
    },
    mutations: {
        setAll(state,payload){
            state.eventTable = payload
        },
        addEvent(state,payload){
            state.eventTable.push(payload)
        }
    },
    actions: {
        async changeEvent({commit},payload){
            try {
                await api.updateEvent(payload)
                var resp = await api.getAll()
                commit("setAll",resp.data.result)
            } catch (error) {
                throw error
            }
        },
        async getEvent({ commit }) {
            try {
                const resp = await api.getAll()
                commit("setAll",resp.data.result)
            } catch (error) {
                throw error
            }
        },
        async createEvent({ commit },payload) {
            try {
                await api.createEvent(payload)
                var resp = await api.getAll()
                commit("setAll",resp.data.result)
            } catch (error) {
                throw error
            }
        },
        async deleteEvent({ commit },payload) {
            try {
                await api.deleteEvent(payload)
                var resp = await api.getAll()
                commit("setAll",resp.data.result)
            } catch (error) {
                throw error
            }
        }
    }
}


